import React from 'react';

export default function MaxOrder({ orderReq }) {

        if (Boolean(orderReq.date_start)) {
                return (
                        <>
                                <caption>
                                          {orderReq?.date_start}  {orderReq?.date_end}
                                </caption>
                                <thead>
                                        <tr>
                                                <th rowSpan={3}>-421</th>
                                                {orderReq?.lessons.map((item) => (
                                                        <th>
                                                                {new Date(
                                                                        item.lesson_date
                                                                ).toLocaleDateString("ru", {
                                                                        day: "2-digit",
                                                                        month: "2-digit",
                                                                })}
                                                        </th>
                                                ))}
                                                <th rowSpan={3}></th>
                                                <th rowSpan={3}> ( )</th>
                                                <th rowSpan={3}> (  )</th>
                                                <th rowSpan={3}> ( )</th>
                                                <th rowSpan={3}>  </th>
                                        </tr>
                                        <tr>
                                                {orderReq?.lessons.map((item) => (
                                                        <th style={{ maxWidth: "200px" }}>
                                                                {item.lesson_name}
                                                        </th>
                                                ))}
                                        </tr>
                                        <tr>
                                                {orderReq?.lessons.map((item) => (
                                                        <th>{item.lesson_number}</th>
                                                ))}
                                        </tr>
                                </thead>
                                <tbody>
                                        {orderReq?.students.map((item) => (
                                                <tr>
                                                        <td style={{ width: "300px" }}>
                                                                {item.student_fullname}
                                                        </td>
                                                        {item.logs.map((item) => (
                                                                <td>{item || " "}</td>
                                                        ))}
                                                        <td>{item.total.delays}</td>
                                                        <td>{item.total.respectfully}</td>
                                                        <td>{item.total.disrespectful}</td>
                                                        <td>{item.total.disease}</td>
                                                        <td>
                                                                {item.total.respectfully +
                                                                        item.total.disrespectful +
                                                                        item.total.disease}
                                                        </td>
                                                </tr>
                                        ))}
                                </tbody>
                                <tfoot>
                                        <tr>
                                                <th></th>
                                                <td colSpan={4}> . .</td>
                                                <td colSpan={3}> </td>
                                        </tr>
                                        <tr>
                                                <th></th>
                                                <td colSpan={4}> . .</td>
                                                <td colSpan={3}> </td>
                                        </tr>
                                </tfoot>
                        </>
                )
        }
        else {
                return (<>FALSE</>)
        }
}
