import React from 'react'

export default function MinOrder({ orderReq }) {
        if (Boolean(orderReq.date_start)) {
                return (
                        <>
                                <caption>
                                          {orderReq?.date_start}  {orderReq?.date_end}
                                </caption>
                                <thead>
                                        <tr>
                                                <th rowSpan={1}>-421</th>
                                                <th rowSpan={1}></th>
                                                <th rowSpan={1}> ( )</th>
                                                <th rowSpan={1}> (  )</th>
                                                <th rowSpan={1}> ( )</th>
                                                <th rowSpan={1}>  </th>
                                        </tr>
                                </thead>
                                <tbody>
                                        {orderReq?.students.map((item) => (
                                                <tr>
                                                        <td style={{ width: "300px" }}>
                                                                {item.student_fullname}
                                                        </td>
                                                        <td>{item.total.delays}</td>
                                                        <td>{item.total.respectfully}</td>
                                                        <td>{item.total.disrespectful}</td>
                                                        <td>{item.total.disease}</td>
                                                        <td>
                                                                {item.total.respectfully +
                                                                        item.total.disrespectful +
                                                                        item.total.disease}
                                                        </td>
                                                </tr>
                                        ))}
                                </tbody>
                                <tfoot>
                                        <tr>
                                                <th></th>
                                                <td colSpan={3}> . .</td>
                                                <td colSpan={2}> </td>
                                        </tr>
                                        <tr>
                                                <th></th>
                                                <td colSpan={3}> . .</td>
                                                <td colSpan={2}> </td>
                                        </tr>
                                </tfoot>
                        </>
                )
        }
        else {
                return (<>FALSE</>)
        }
}
